/* ===== Sección Sobre Mí ===== */
.sobre-mi {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.95), rgba(220, 235, 240, 0.95)),
  url("/src/assets/images/2.png") no-repeat center/cover;
  padding: 100px 5%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: auto;
  margin: 0 auto;
  position: relative;
}

/* Contenedor general para dividir texto e imagen */
.sobre-mi-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  max-width: 1200px;
  flex-wrap: wrap;
  gap: 30px;
}

/* Estilos para el texto */
.text-content {
  flex: 1;
  z-index: 2;
  max-width: 800px;
  padding: 20px;
  text-align: center;
}

.text-content h1 {
  font-size: 2.8rem;
  font-weight: bold;
  color: #004d73;
  margin-bottom: 15px;
}

.text-content h2 {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 15px;
  color: #173b4e;
}

.text-content p {
  font-size: 1.2rem;
  line-height: 1.8;
  color: #333;
  margin: 10px 0;
}

/* Lista de beneficios */
.benefits-list {
  list-style: none; /* Elimina los puntos */
  padding: 0;
  margin: 20px 0;
}

.benefits-list li {
  font-size: 1.2rem;
  line-height: 1.8;
  margin: 5px 0;
}

/* Contenedor de la imagen */
.image-container-vicente {
  display: flex;
  justify-content: center; /* Centra la imagen horizontalmente */
  align-items: center;     /* Centra la imagen verticalmente */
  width: 100%;
  max-width: 600px;        /* Ajusta el max-width según sea necesario */
  margin: 0 auto;          /* Centra el contenedor en su padre */
  padding: 0 20px;         /* Ajusta el padding si es necesario */
  position: relative;
}


/* Imagen dentro del contenedor */
.image-container-vicente img {
  width: auto;
  max-width: 100%; /* Asegura que no exceda el tamaño del contenedor */
  height: auto;
  border-radius: 10px;
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.2);
  object-fit: cover;
}

/* Ajustes para pantallas medianas */
@media (max-width: 900px) {
  .sobre-mi-content {
    flex-direction: column;
    text-align: center;
  }

  .text-content {
    order: 2;
    text-align: center;
  }

  .image-container {
    order: 1;
    justify-content: center;
    margin-bottom: 20px;
  }

  .image-container img {
    max-width: 250px;
  }
}

/* Ajustes para pantallas pequeñas */
@media (max-width: 600px) {
  .text-content h1 {
    font-size: 2.2rem;
  }

  .text-content h2 {
    font-size: 1.5rem;
  }

  .text-content p, .benefits-list li {
    font-size: 1rem;
  }

  .btn-contacto {
    font-size: 1rem;
    padding: 10px 15px;
  }

  .image-container img {
    max-width: 200px;
  }
}
